import axios from 'axios';
import {
  loginError,
  loginRequest,
  loginSuccess,
  refreshRequest,
  refreshSuccess,
  refreshError,
  logOut,
} from './sessionActions';
import { getToken } from './sessionSelectors';

axios.defaults.baseURL = 'https://cpp.infoxvod.com.ua/test_api';

const setAuthToken = token => {
  axios.defaults.headers.common.Autorization = `Bearer ${token}`;
};

const clearAuthToken = token => {
  axios.defaults.headers.common.Autorization = null;
};

// axios.defaults.headers.common.Authorization = null;

export const login = credentials => dispatch => {
  dispatch(loginRequest());
  const dataAuth = {
    login: credentials.mail,
    password: credentials.password,
    rememberMe: credentials.remmember,
  };
  console.log(dataAuth);

  axios({
    method: 'post',
    url: '/login.php',
    // headers: {
    //   AccessControlAllowOrigin: '*',
    //   ContentType: 'application/json',
    //   withCredentials: true,
    //   mode: 'no-cors',
    // },
    data: {
      login: credentials.mail,
      password: credentials.password,
      rememberMe: credentials.remmember,
    },
  })
    // .post('/login.php', dataAuth)
    .then(response => {
      console.log(response);
      // setAuthToken(response.data.data.token);
      dispatch(loginSuccess(response.data.data));
    })
    .catch(error => {
      console.log(error);
      clearAuthToken();
      dispatch(loginError(error));
    });
};

export const refreshUser = () => (dispatch, getState) => {
  const token = getToken(getState());
  if (!token) return;
  dispatch(refreshRequest());
  axios
    .get('/')
    .then(response => {
      console.log(response.data.data);
      // setAuthToken(response.data.data.token);
      dispatch(refreshSuccess(response.data.data));
    })
    .catch(error => {
      clearAuthToken();
      console.log(error);
      dispatch(refreshError(error));
    });
};

export const logOutOperation = () => dispatch => {
  clearAuthToken();
  dispatch(logOut());
};
